import React, { useEffect } from 'react';
import {
  Tab,
  Paper,
  Container,
  makeStyles,
  Theme,
  fade,
} from '@material-ui/core';
import { TabContext, TabList, TabPanel } from '@material-ui/lab';

import HeadBar from '@/Components/HeadBar';
import Upload from '@/Components/Upload';
import useUser from '@/Contexts/User';

import Logo from '@/Assets/Images/Global/Logo.png';
import request from '@/Utils/Request';
import api from '@/Constants/api';
import Loading from '@/ImperativeComponents/Loading';
import Message from '@/ImperativeComponents/Message';

const index: React.FC = ({ children }) => {
  const [value, setValue] = React.useState('profile');
  const { user, updateProfile } = useUser();

  const useStyles = makeStyles((theme: Theme) => ({
    root: {
      width: '100%',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',

      '& .avatar-wrapper': {
        width: 150,
        height: 150,
        borderRadius: 75,
        overflow: 'hidden',
        position: 'relative',
        marginBottom: theme.spacing(4),

        [theme.breakpoints.up('md')]: {
          width: 200,
          height: 200,
          borderRadius: 100,
          marginBottom: theme.spacing(8),
        },

        '& .avatar': {
          width: '100%',
          height: '100%',
        },

        '& .avatar-cover': {
          position: 'absolute',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          left: 0,
          right: 0,
          top: 0,
          bottom: 0,
          backgroundColor: fade(theme.palette.common.black, 0.5),
          opacity: 0,
          transition: '0.3s',

          '&:hover': {
            opacity: 1,
          },
        },
      },

      '& .tabs-wrapper': {
        height: 300,

        '& .tabs': {
          borderRight: `1px solid ${theme.palette.divider}`,
        },
      },
    },
  }));
  const classes = useStyles();

  const handleUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const avatarFile = event.target.value;
    if (avatarFile) {
      const loading = new Loading();
      const form = new FormData();
      form.append('avatar', avatarFile);
      request.post(api.user.profile.uploadAvatar, form).then((res) => {
        loading.close();
        if (res.status === 200) {
          const { avatar } = res.data;
          const updatedProfile = {
            ...user?.profile,
            avatar,
          };
          updateProfile(updatedProfile);
          new Message({
            type: 'success',
            content: '更新头像成功',
          });
        } else {
          new Message({
            type: 'error',
            content: '更新头像失败',
          });
        }
      }).catch(() => {
        loading.close();
        new Message({
          type: 'error',
          content: '更新头像失败',
        });
      });
    }
  };

  return (
    <div className={classes.root}>
      <HeadBar position="fixed" />
      <Upload accept="image/*" className="avatar-wrapper" onChange={handleUpload}>
        <img src={user?.profile.avatar || Logo} className="avatar" />
        <div role="button" className="avatar-cover">上传头像</div>
      </Upload>
      <Container maxWidth="md">
        <TabContext value={value}>
          <Paper square className="tabs-wrapper">
            <TabList
              className="tabs"
              variant="fullWidth"
              indicatorColor="primary"
              onChange={(event, newValue) => setValue(newValue)}
            >
              <Tab label="基本信息" value="profile" />
              <Tab label="账号安全" value="account" />
              <Tab label="第三方登录" value="oauth" />
            </TabList>
            <TabPanel value="profile">
              Item One
            </TabPanel>
            <TabPanel value="account">
              Item Two
            </TabPanel>
            <TabPanel value="oauth">
              Item Three
            </TabPanel>
          </Paper>
        </TabContext>
      </Container>
    </div>
  );
};

export default index;
