import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core';

interface RenderProps {
  value?: File;
  values?: File[];
}

interface UploadProps {
  name?: string;
  accept?: string;
  multiple?: boolean;
  className?: string;
  value?: File[];
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  render?: React.FC<RenderProps>;
  getSingleFile?: boolean;
  keepChosenFiles?: boolean;
}

const upload: React.FC<UploadProps> = (props) => {
  const [files, setFiles] = useState<File[]>([]);

  const {
    name,
    accept,
    multiple,
    className,
    value,
    onChange,
    render,
    getSingleFile = true,
    keepChosenFiles = false,
    children,
  } = props;

  const useStyles = makeStyles(() => ({
    input: {
      display: 'none',
    },
  }));
  const classes = useStyles();

  useEffect(() => {
    setFiles(value || []);
  }, []);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    let newFiles;
    if (keepChosenFiles) {
      newFiles = [...files, ...Array.from(event.target.files || [])];
    } else {
      newFiles = Array.from(event.target.files || []);
    }
    const parsedEvent = {
      ...event,
      target: {
        ...event.target,
        value: (getSingleFile ? newFiles[0] : newFiles) as any,
      },
    };
    onChange?.(parsedEvent);
    setFiles(newFiles);
  };

  return (
    <label className={className}>
      <input
        name={name}
        type="file"
        accept={accept}
        multiple={multiple}
        className={classes.input}
        onChange={handleChange}
      />
      {render ? render({ value: files[0], values: files }) : children}
    </label>
  );
};

export default upload;
