import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import {
  Button,
  TextField,
  FormControlLabel,
  Switch,
  createStyles,
  makeStyles,
  Theme,
} from '@material-ui/core';

import Message from '@/ImperativeComponents/Message';
import useUser from '@/Contexts/User';
import api from '@/Constants/api';
import request from '@/Utils/Request';

interface LoginForm {
  identifier: string,
  password: string,
  keepAuthenticated: boolean,
}

const index: React.FC = () => {
  const { control, handleSubmit, register } = useForm<LoginForm>();
  const history = useHistory();
  const { login } = useUser();
  const [loading, setLoading] = useState(false);

  const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
      '& .form-controller': {
        marginTop: theme.spacing(1),
        marginBottom: theme.spacing(1),

        '&:first-child': {
          marginTop: 0,
        },

        '&:last-child': {
          marginBottom: 0,
        },
      },

      '& .switch-button': {
        display: 'block',
        marginLeft: 'auto',
        marginRight: 0,
      },
    },
  }));
  const classes = useStyles();

  const switchToRegister = () => {
    history.push('/user/auth/register');
  };

  const onSubmit = (data: LoginForm) => {
    const params = { identifier: data.identifier, password: data.password };
    setLoading(true);
    request.post(api.user.auth.login, params).then((res) => {
      setLoading(false);
      if (res.status === 200) {
        login({
          user: res.data.user,
          keepAuthenticated: data.keepAuthenticated,
          token: res.data.token,
        });
        new Message({
          type: 'success',
          content: '登录成功',
        });
      } else {
        new Message({
          type: 'error',
          content: '登录失败',
        });
      }
    }).catch(() => {
      setLoading(false);
      new Message({
        type: 'error',
        content: '登录失败',
      });
    });
  };

  return (
    <form className={classes.root} onSubmit={handleSubmit(onSubmit)}>
      <Controller
        name="identifier"
        control={control}
        defaultValue=""
        className="form-controller"
        as={(
          <TextField
            label="用户名或手机号码或邮箱"
            required
            fullWidth
          />
        )}
      />
      <Controller
        name="password"
        control={control}
        defaultValue=""
        className="form-controller"
        as={(
          <TextField
            label="密码"
            type="password"
            required
            fullWidth
          />
        )}
      />
      <FormControlLabel
        label="记住密码"
        control={(
          <Switch
            name="keepAuthenticated"
            inputRef={register}
            color="primary"
          />
        )}
      />
      <Button
        variant="contained"
        color="primary"
        type="submit"
        disabled={loading}
        className="form-controller"
        fullWidth
      >
        {loading ? '登录中' : '登录'}
      </Button>
      {/* <Button
        color="primary"
        className="form-controller switch-button"
        onClick={switchToRegister}
      >
        切换到注册
      </Button> */}
    </form>
  );
};

export default index;
