import React, { ReactElement } from 'react';
import {
  Button,
  ButtonBase,
  Popover,
  createStyles,
  makeStyles,
} from '@material-ui/core';

interface DropdownPanelProps {
  buttonText?: string;
  buttonColor?: 'default' | 'inherit' | 'primary' | 'secondary';
  buttonFullWith?: boolean;
  buttonSize?: 'large' | 'medium' | 'small'
  buttonVariant?: 'contained' | 'outlined' | 'text';
  dropdownElement?: ReactElement;
  buttonClassName?: string;
}

const dropdownPanel: React.FC<DropdownPanelProps> = (props) => {
  const useStyles = makeStyles(() => createStyles({
    root: {
      verticalAlign: 'inherit',
    },
  }));
  const classes = useStyles();

  const [anchor, setAnchor] = React.useState<null | Element>(null);

  const {
    buttonText = '下拉选择',
    buttonColor = 'default',
    buttonFullWith = false,
    buttonSize = 'medium',
    buttonVariant = 'text',
    dropdownElement,
    buttonClassName,
    children,
  } = props;

  const open = (event: React.MouseEvent<HTMLElement>) => {
    setAnchor(event.currentTarget);
  };

  const close = () => {
    setAnchor(null);
  };

  return (
    <>
      {dropdownElement ? (
        <ButtonBase
          component="div"
          onClick={open}
          disableRipple
          disableTouchRipple
          className={`${buttonClassName} ${classes.root}`}
        >
          {dropdownElement}
        </ButtonBase>
      ) : (
        <Button
          onClick={open}
          color={buttonColor}
          fullWidth={buttonFullWith}
          size={buttonSize}
          variant={buttonVariant}
        >
          {buttonText}
        </Button>
      )}
      <Popover
        keepMounted
        anchorEl={anchor}
        open={Boolean(anchor)}
        onClose={close}
      >
        <div onMouseLeave={close}>
          {children}
        </div>
      </Popover>
    </>
  );
};

export default dropdownPanel;
