import {
  Backdrop,
  CircularProgress,
  createStyles,
  makeStyles,
} from '@material-ui/core';
import React, {
  useState,
  forwardRef,
  useImperativeHandle,
  RefObject,
} from 'react';
import ReactDOM from 'react-dom';

import { ThemeProvider } from '@/Contexts/Theme';

interface LoadingMethods {
  close(): void;
}

const LoadingComponent = forwardRef<LoadingMethods>((props, ref) => {
  const [open, setOpen] = useState(true);

  const useStyles = makeStyles(() => createStyles({
    root: {
      zIndex: 10000,
    },
  }));
  const classes = useStyles();

  useImperativeHandle(ref, () => ({
    close: () => {
      setOpen(false);
    },
  }));

  return (
    <Backdrop className={classes.root} open={open}>
      <CircularProgress color="primary" />
    </Backdrop>
  );
});

class Loading {
  container: HTMLDivElement;

  component: RefObject<LoadingMethods>;

  constructor() {
    this.container = document.createElement('div');
    document.body.appendChild(this.container);
    this.component = React.createRef();
    ReactDOM.render(
      <ThemeProvider>
        <LoadingComponent ref={this.component} />
      </ThemeProvider>,
      this.container,
    );
    return this;
  }

  close(): void {
    this.component?.current?.close();
    setTimeout(() => {
      ReactDOM.unmountComponentAtNode(this.container);
      document.body.removeChild(this.container);
    }, 1000);
  }
}

export default Loading;
