import React, { useEffect, useState } from 'react';
import {
  Card,
  CardHeader,
  Avatar,
  Button,
  CardActions,
  Divider,
  IconButton,
  Container,
  TextField,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Input,
  Theme,
  createStyles,
  makeStyles,
} from '@material-ui/core';
import { MoreVert as MenuIcon } from '@material-ui/icons';
import { Controller, useForm } from 'react-hook-form';

import Loading from '@/ImperativeComponents/Loading';
import Message from '@/ImperativeComponents/Message';
import Dialog from '@/ImperativeComponents/Dialog';
import HeadBar from '@/Components/HeadBar';
import Empty from '@/Components/Empty';
import api from '@/Constants/api';
import request from '@/Utils/Request';
import Upload from '@/Components/Upload';

interface FontStyle {
  id: number;
  name: string;
}

interface Font {
  id: number;
  fontFile: string;
  fontStyle: FontStyle;
  previewImage: string;
}

interface FontForm {
  id: number;
  font?: File;
  fontStyleId?: number;
  fontFamilyId?: number;
}

interface FontStyle {
  id: number,
  name: string,
}

interface FontFamily {
  id: number;
  name: string;
  language: number;
  fonts: Font[];
}

const useStyles = makeStyles((theme: Theme) => createStyles({
  root: {
    '& .container': {
      display: 'flex',
      flexWrap: 'wrap',
      paddingTop: theme.spacing(1),
      paddingBottom: theme.spacing(1),

      [theme.breakpoints.only('xs')]: {
        width: 320,
      },

      '& .card-wrapper': {
        width: '100%',
        padding: theme.spacing(1),

        [theme.breakpoints.up('sm')]: {
          width: '50%',
        },

        [theme.breakpoints.up('md')]: {
          width: 'calc(100% / 3)',
        },

        [theme.breakpoints.up('lg')]: {
          width: '25%',
        },

        [theme.breakpoints.up('xl')]: {
          width: 'calc(100% / 6)',
        },

        '& .card-avatar': {
          backgroundColor: theme.palette.primary.main,
          color: theme.palette.primary.contrastText,
        },

        '& .card-media': {
          width: '100%',
          padding: theme.spacing(1),
          filter: theme.palette.type === 'dark' ? 'invert(100%)' : undefined,
        },
      },
    },
  },

  editFontFamily: {
    '& .form-controller': {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),

      '&:first-child': {
        marginTop: 0,
      },

      '&:last-child': {
        marginBottom: 0,
      },
    },
  },

  editFonts: {
    '& .form-controller': {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),

      '&:first-child': {
        marginTop: 0,
      },

      '&:last-child': {
        marginBottom: 0,
      },
    },
  },

  fonts: {
    width: 320,
    maxWidth: '100%',
    padding: theme.spacing(1),
    borderRadius: theme.shape.borderRadius,
    backgroundColor: theme.palette.background.default,

    '& .font': {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),

      '&:first-child': {
        marginTop: 0,
      },

      '&:last-child': {
        marginBottom: 0,
      },

      '& .font-image': {
        width: 240,
        margin: theme.spacing(1),
        marginBottom: 0,
        filter: theme.palette.type === 'dark' ? 'invert(100%)' : undefined,
      },

      '& .font-actions': {
        display: 'flex',
        justifyContent: 'space-between',

        '& .font-style': {
          marginLeft: theme.spacing(1),
        },
      },
    },
  },
}));

const index: React.FC = () => {
  const [fontFamilyList, setFontFamilyList] = useState<FontFamily[]>([]);
  const [fontStyleList, setFontStyleList] = useState<FontStyle[]>([]);
  const [searchKeyword, setSearchKeyword] = useState('');
  const fontFamilyForm = useForm<FontFamily>();
  const fontForm = useForm<FontForm>();
  const classes = useStyles();

  useEffect(() => {
    const loading = new Loading();
    request.get(api.fontLibrary.fontFamily.list, {
      params: searchKeyword ? {
        name: searchKeyword,
      } : undefined,
    }).then((res) => {
      loading.close();
      if (res.status === 200) {
        setFontFamilyList(res.data.list);
      } else {
        new Message({
          type: 'error',
          content: '获取字体失败',
        });
      }
    }).catch(() => {
      loading.close();
      new Message({
        type: 'error',
        content: '获取字体失败',
      });
    });
  }, [searchKeyword]);

  useEffect(() => {
    request.get(api.fontLibrary.fontStyle.list).then((res) => {
      if (res.status === 200) {
        setFontStyleList(res.data.list);
      }
    });
  }, []);

  const editFontFamily = (values: FontFamily) => {
    const loading = new Loading();
    request.post(api.fontLibrary.fontFamily.update, values, {
      timeout: 10 * 60 * 1000,
    }).then((updateRes) => {
      loading.close();
      if (updateRes.status === 200) {
        new Message({
          type: 'success',
          content: '更新字体族成功',
        });
        request.get(api.fontLibrary.fontFamily.list).then((getListRes) => {
          if (getListRes.status === 200) {
            setFontFamilyList(getListRes.data.list);
          }
        });
      }
    }).catch(() => {
      loading.close();
      new Message({
        type: 'error',
        content: '更新字体族失败',
      });
    });
  };

  const editFont = (values: FontForm) => {
    console.log(values);
  };

  const removeFontFamily = (fontFamily: FontFamily) => {
    new Dialog({
      title: '操作确认',
      content: `是否确认删除字体“${fontFamily.name}”？`,
      onConfirm: () => {
        const loading = new Loading();
        request.post(api.fontLibrary.fontFamily.remove, {
          id: fontFamily.id,
        }).then((removeRes) => {
          loading.close();
          if (removeRes.status === 200) {
            new Message({
              type: 'success',
              content: '删除字体族成功',
            });
            request.get(api.fontLibrary.fontFamily.list).then((getListRes) => {
              if (getListRes.status === 200) {
                setFontFamilyList(getListRes.data.list);
              }
            });
          } else {
            new Message({
              type: 'error',
              content: '删除字体族失败',
            });
          }
        }).catch(() => {
          loading.close();
          new Message({
            type: 'error',
            content: '删除字体族失败',
          });
        });
      },
    });
  };

  const showEditFontFamilyDialog = (fontFamily: FontFamily) => {
    fontFamilyForm.reset(fontFamily);
    const dialog: Dialog = new Dialog({
      title: '编辑字体族',
      content: (
        <form className={classes.editFontFamily}>
          <Controller
            name="id"
            control={fontFamilyForm.control}
            rules={{ required: true }}
            className="form-controller"
            as={(
              <TextField
                label="字体族 ID"
                disabled
                required
                fullWidth
              />
            )}
          />
          <Controller
            name="name"
            control={fontFamilyForm.control}
            rules={{ required: true }}
            className="form-controller"
            as={(
              <TextField
                label="字体族名称"
                required
                fullWidth
              />
            )}
          />
          <Controller
            name="language"
            control={fontFamilyForm.control}
            className="form-controller"
            as={(
              <TextField
                select
                label="选择语言"
                required
                fullWidth
              >
                <MenuItem value={1}>中文</MenuItem>
                <MenuItem value={2}>英文</MenuItem>
              </TextField>
            )}
          />
        </form>
      ),
      onConfirm: async () => {
        const valid = await fontFamilyForm.trigger();
        if (valid) {
          dialog.close();
          fontFamilyForm.handleSubmit(editFontFamily)();
        } else {
          new Message({
            type: 'error',
            content: '校验失败',
          });
        }
      },
      onCancel: () => dialog.close(),
      closeOnClick: false,
    });
  };

  const showEditFontsDialog = (fontFamily: FontFamily) => {
    const dialog: Dialog = new Dialog({
      title: fontFamily.name,
      content: (
        <div className={classes.fonts}>
          {fontFamily.fonts.map((font) => (
            <Card key={font.id} className="font">
              <img className="font-image" src={font.previewImage} />
              <CardActions className="font-actions">
                <div>
                  字体样式：
                  {font.fontStyle.name}
                </div>
                <Button
                  color="primary"
                  className="font-style"
                  onClick={() => {
                    fontForm.reset(font);
                    dialog.change({
                      title: '编辑字体',
                      content: (
                        <form className={classes.editFonts}>
                          <Controller
                            name="id"
                            control={fontForm.control}
                            rules={{ required: true }}
                            className="form-controller"
                            as={(
                              <TextField
                                label="字体 ID"
                                disabled
                                required
                                fullWidth
                              />
                            )}
                          />
                          <Controller
                            name="font"
                            control={fontForm.control}
                            className="form-controller"
                            as={(
                              <Upload
                                render={({ value }) => (
                                  <>
                                    <div>上传</div>
                                    {value?.name}
                                  </>
                                )}
                              />
                            )}
                          />
                          <Controller
                            name="fontStyleId"
                            control={fontForm.control}
                            className="form-controller"
                            as={(
                              <TextField
                                select
                                label="选择字体样式"
                                fullWidth
                              >
                                {fontStyleList.map((fontStyle) => (
                                  <MenuItem key={fontStyle.id} value={fontStyle.id}>
                                    {fontStyle.name}
                                  </MenuItem>
                                ))}
                              </TextField>
                            )}
                          />
                        </form>
                      ),
                      onConfirm: async () => {
                        const valid = await fontForm.trigger();
                        if (valid) {
                          dialog.close();
                          fontForm.handleSubmit(editFont)();
                        } else {
                          new Message({
                            type: 'error',
                            content: '校验失败',
                          });
                        }
                      },
                      onCancel: () => dialog.close(),
                      closeOnClick: false,
                    });
                  }}
                >
                  编辑
                </Button>
              </CardActions>
            </Card>
          ))}
        </div>
      ),
      showCancel: false,
    });
  };

  return (
    <div className={classes.root}>
      <HeadBar
        title="字体库管理"
        searchText="搜索字体名称"
        onSearch={(keyword) => {
          setSearchKeyword(keyword);
        }}
        searchThrottle={500}
        changeTransparencyOnScroll={false}
      />
      <Container fixed maxWidth="xl" className="container">
        {fontFamilyList.length ? fontFamilyList.map((fontFamily) => (
          <div key={fontFamily.id} className="card-wrapper">
            <Card>
              <CardHeader
                avatar={
                  <Avatar className="card-avatar">{fontFamily.language === 1 ? '中' : '英'}</Avatar>
                }
                title={fontFamily.name}
                titleTypographyProps={{ noWrap: true }}
                subheader={`共 ${fontFamily.fonts.length} 种样式可供下载`}
                action={(
                  <IconButton onClick={() => showEditFontsDialog(fontFamily)}>
                    <MenuIcon />
                  </IconButton>
                )}
              />
              {Boolean(fontFamily.fonts.length) && (
                <img className="card-media" src={fontFamily.fonts[0].previewImage} />
              )}
              <CardActions>
                <Button
                  color="primary"
                  onClick={() => showEditFontFamilyDialog(fontFamily)}
                >
                  编辑字体族
                </Button>
                <Button
                  color="primary"
                  onClick={() => removeFontFamily(fontFamily)}
                >
                  删除字体族
                </Button>
              </CardActions>
            </Card>
          </div>
        )) : (
          <Empty description="没有符合条件的字体" />
        )}
      </Container>
    </div>
  );
};

export default index;
