import React, {
  useEffect,
  useRef,
  useState,
} from 'react';
import { useHistory } from 'react-router-dom';
import {
  AppBar,
  IconButton,
  Toolbar,
  Typography,
  InputBase,
  MenuList,
  MenuItem,
  Divider,
  createStyles,
  makeStyles,
  Theme,
  fade,
} from '@material-ui/core';
import {
  Menu as MenuIcon,
  Brightness5 as DarkModeOffIcon,
  Brightness4 as DarkModeOnIcon,
  BrightnessAuto as DarkModeAutoIcon,
  AccountCircle as UserIcon,
  Search as SearchIcon,
} from '@material-ui/icons';

import DropdownPanel from '@/Components/DropdownPanel';
import useUser from '@/Contexts/User';
import useDrawer from '@/Contexts/Drawer';
import useTheme from '@/Contexts/Theme';
import useDebounce from '@/Utils/Debounce';
import Logo from '@/Assets/Images/Global/Logo.png';

type ChangeTransparencyScrollHeight = `${number}vh` | number;

interface HeadBarProps {
  title?: string;
  position?: 'static' | 'sticky' | 'fixed';
  searchText?: string;
  onSearch?: (keyword: string) => unknown;
  searchThrottle?: number;
  transparency?: number;
  changeTransparencyOnScroll?: boolean;
  changeTransparencyScrollHeight?: ChangeTransparencyScrollHeight;
}

const headBar: React.FC<HeadBarProps> = (props: HeadBarProps) => {
  const { user, logout } = useUser();
  const { darkMode, setDarkMode } = useTheme();
  const drawer = useDrawer();
  const history = useHistory();

  const [originalTop, setOriginalTop] = useState(0);
  const [relativeTransparency, setRelativeTransparency] = useState(1);
  const [changeTransparencyScrollHeight, setChangeTransparencyScrollHeigh] = useState(0);
  const ref = useRef<Element>();

  const {
    title,
    position = 'sticky',
    searchText = '请输入关键词',
    onSearch,
    searchThrottle,
    transparency: maxTransparency = 1,
    changeTransparencyOnScroll = true,
    changeTransparencyScrollHeight: rawChangeTransparencyScrollHeight = '100vh',
  } = props;

  const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
      color: theme.palette.text.primary,
      backgroundColor: fade(
        theme.palette.background.paper, relativeTransparency * Math.min(maxTransparency, 1),
      ),
      boxShadow: `0px 2px 4px -1px rgb(0 0 0 / ${20 * relativeTransparency * Math.min(maxTransparency, 1)}%),
        0px 4px 5px -1px rgb(0 0 0 / ${14 * relativeTransparency * Math.min(maxTransparency, 1)}%),
        0px 1px 10px -1px rgb(0 0 0 / ${12 * relativeTransparency * Math.min(maxTransparency, 1)}%)`,
      transition: 'box-shadow 0s',

      '& .title': {
        marginLeft: theme.spacing(2),
        marginRight: onSearch ? 0 : theme.spacing(2),
        flexGrow: 1,
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        display: onSearch ? 'none' : 'block',

        [theme.breakpoints.up('sm')]: {
          display: 'block',
        },
      },

      '& .search': {
        width: 0,
        flexGrow: 1,
        flexShrink: 0,
        position: 'relative',
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.grey[500], 0.15),
        marginLeft: theme.spacing(2),
        marginRight: theme.spacing(2),

        '&:hover': {
          backgroundColor: fade(theme.palette.grey[500], 0.25),
        },

        '&:focus-within': {
          backgroundColor: fade(theme.palette.grey[500], 0.25),
        },

        [theme.breakpoints.up('sm')]: {
          flexGrow: 'unset',
          width: 200,

          '&:hover': {
            width: 250,
          },

          '&:focus-within': {
            width: 250,
          },
        },

        '& .search-icon': {
          padding: theme.spacing(0, 2),
          height: '100%',
          position: 'absolute',
          pointerEvents: 'none',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        },

        '& .input-root': {
          color: 'inherit',

          '& .input-content': {
            padding: theme.spacing(1, 1, 1, 0),
            paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
          },
        },
      },

      '& .animate': {
        transition: '0.3s',
      },
    },

    userInfo: {
      width: 256,
      padding: theme.spacing(2),
      display: 'flex',
      alignItems: 'center',

      '& .user-avatar': {
        width: 64,
        height: 64,
        borderRadius: 32,
      },

      '& .user-info-text': {
        marginLeft: theme.spacing(2),
        width: 0,
        flexGrow: 1,

        '& .user-nickname': {
          fontSize: '1rem',
          marginBottom: theme.spacing(1),
        },

        '& .user-intro': {
          fontSize: '0.75rem',
          overflow: 'hidden',
          textOverflow: 'ellipsis',
          display: '-webkit-box',
          '-webkit-line-clamp': 2,
          '-webkit-box-orient': 'vertical',
        },
      },
    },
  }));
  const classes = useStyles();

  const debouncedSearch = onSearch
    ? useDebounce(onSearch, searchThrottle || 0, { trailing: true })
    : () => undefined;

  // 随滚动高度改变透明度
  const changeRelativeTransparency = () => {
    setRelativeTransparency(
      Math.min(
        Math.max((document.getElementById('root')?.scrollTop || 0) - originalTop, 0) / changeTransparencyScrollHeight,
        1,
      ),
    );
  };

  // 页面初始化时计算初始滚动高度
  useEffect(() => {
    if (position === 'sticky') {
      const headBarHeight = ref.current?.getBoundingClientRect?.()?.height;
      const nextElement = (ref.current?.nextElementSibling as HTMLElement);
      const currentOriginalTop = nextElement
        ? nextElement.offsetTop
          - Number(headBarHeight)
          - Number(getComputedStyle(nextElement).marginBottom?.replace('px', ''))
        : 0;
      setOriginalTop(currentOriginalTop);
    }
  }, []);

  // 如果设置了滚动高度阈值则采用设置的阈值
  useEffect(() => {
    if (typeof rawChangeTransparencyScrollHeight === 'string') {
      setChangeTransparencyScrollHeigh(
        (
          Number(rawChangeTransparencyScrollHeight.replace('vh', '')) / 100
        ) * window.innerHeight,
      );
    } else if (typeof rawChangeTransparencyScrollHeight === 'number') {
      setChangeTransparencyScrollHeigh(rawChangeTransparencyScrollHeight);
    }
  }, [rawChangeTransparencyScrollHeight]);

  // 在初始滚动高度或滚动高度阈值改变时重设滚动监听器
  useEffect(() => {
    document.getElementById('root')?.removeEventListener('scroll', changeRelativeTransparency);
    if (position !== 'static' && changeTransparencyOnScroll) {
      document.getElementById('root')?.addEventListener('scroll', changeRelativeTransparency);
      changeRelativeTransparency();
    }
    return () => document.getElementById('root')?.removeEventListener('scroll', changeRelativeTransparency);
  }, [originalTop, changeTransparencyScrollHeight]);

  return (
    <AppBar color="inherit" position={position} ref={ref} className={classes.root}>
      <Toolbar>
        <IconButton edge="start" onClick={() => drawer.open()}>
          <MenuIcon />
        </IconButton>
        <Typography className="title" variant="h6">
          {title}
        </Typography>
        {onSearch && (
        <div className="search animate">
          <div className="search-icon">
            <SearchIcon />
          </div>
          <InputBase
            onChange={(event) => debouncedSearch(event.target.value)}
            placeholder={searchText}
            classes={{
              root: 'input-root animate',
              input: 'input-content',
            }}
          />
        </div>
        )}
        <DropdownPanel
          dropdownElement={(
            <IconButton>
              {darkMode === 'auto' ? <DarkModeAutoIcon /> : (
                darkMode ? <DarkModeOnIcon /> : <DarkModeOffIcon />
              )}
            </IconButton>
          )}
        >
          <MenuList>
            {darkMode !== true && (
              <MenuItem onClick={() => setDarkMode(true)}>开启夜间模式</MenuItem>
            )}
            {darkMode !== false && (
              <MenuItem onClick={() => setDarkMode(false)}>关闭夜间模式</MenuItem>
            )}
            {darkMode !== 'auto' && (
              <MenuItem onClick={() => setDarkMode('auto')}>自动夜间模式</MenuItem>
            )}
          </MenuList>
        </DropdownPanel>
        <DropdownPanel
          dropdownElement={(
            <IconButton edge="end">
              <UserIcon />
            </IconButton>
          )}
        >
          {user ? (
            <>
              <div className={classes.userInfo}>
                <img src={user.profile.avatar || Logo} className="user-avatar" />
                <div className="user-info-text">
                  <div className="user-nickname">{user.profile.nickname || '访客'}</div>
                  <div className="user-intro">
                    {user.profile.intro || '这个人很懒，自我介绍啥也没写。'}
                  </div>
                </div>
              </div>
              <Divider />
              <MenuList>
                <MenuItem onClick={() => history.push('/user/dashboard')}>用户中心</MenuItem>
                <MenuItem onClick={() => logout()}>退出登录</MenuItem>
              </MenuList>
            </>
          ) : (
            <MenuList>
              <MenuItem onClick={() => history.push('/user/auth/login')}>前往登录</MenuItem>
            </MenuList>
          )}
        </DropdownPanel>
      </Toolbar>
    </AppBar>
  );
};

export default headBar;
