import React, { useState } from 'react';
import { makeStyles, createStyles } from '@material-ui/styles';
import {
  Button,
  Paper,
  Theme,
  fade,
} from '@material-ui/core';
import {
  Book as BlogIcon,
  Computer as TechNotebookIcon,
  Image as ImageIcon,
  Brush as ImageToolkitIcon,
  TextFields as FontLibraryIcon,
  People as UserIcon,
} from '@material-ui/icons';
import {
  Avatar,
  Card,
  CardHeader,
  CardMedia,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import Dialog from '@/ImperativeComponents/Dialog';

import BlogImage from '@/Assets/Images/Home/Blog.jpg';
import FontLibraryImage from '@/Assets/Images/Home/FontLibrary.jpg';
import GalleryImage from '@/Assets/Images/Home/Gallery.jpg';
import ImageToolkitImage from '@/Assets/Images/Home/ImageToolkit.jpg';
import TechNotebookImage from '@/Assets/Images/Home/TechNotebook.jpg';
import UserCenterImage from '@/Assets/Images/Home/UserCenter.jpg';

const links = [
  {
    path: '/blog',
    title: '博客',
    subtitle: '一个想起来啥就写啥的地方',
    icon: <BlogIcon />,
    color: '#277DA1',
    image: BlogImage,
  },
  {
    path: null,
    title: '技术笔记本',
    subtitle: '记录有意思的 Bug 和技术方案',
    icon: <TechNotebookIcon />,
    color: '#43AA8B',
    image: TechNotebookImage,
  },
  {
    path: null,
    title: '照片墙',
    subtitle: '自认为拍得还凑合的照片',
    icon: <ImageIcon />,
    color: '#F9C74F',
    image: GalleryImage,
  },
  {
    path: '/imagetoolkit',
    title: '图像工具箱',
    subtitle: '批处理图像还挺方便的工具',
    icon: <ImageToolkitIcon />,
    color: '#F3722C',
    image: ImageToolkitImage,
  },
  {
    path: '/fontlibrary',
    title: '字体库',
    subtitle: '收集了一堆商用免费字体',
    icon: <FontLibraryIcon />,
    color: '#F94144',
    image: FontLibraryImage,
  },
  {
    path: '/user/dashboard',
    title: '用户中心',
    subtitle: '管理你的信息和收藏夹',
    icon: <UserIcon />,
    color: '#54478C',
    image: UserCenterImage,
  },
];

const home: React.FC = () => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const history = useHistory();

  const navigateTo = (path: string | null) => {
    if (!path) {
      new Dialog({
        title: '暂未完成',
        content: '该模块暂未开发完毕，敬请期待。',
        showCancel: false,
      });
    } else {
      history.push(path);
    }
  };

  const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
      width: '100%',
      height: '100%',
      overflow: 'hidden',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'flex-end',
      alignItems: 'center',
      padding: `min(calc(20% / ${links.length}), ${theme.spacing(2)}px)`,

      '& .info-wrapper': {
        width: 800,
        maxWidth: '80%',
        flexGrow: 1,
        margin: theme.spacing(6),
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',

        '& .info': {
          width: '100%',
          height: '100%',
          maxHeight: 'min(100vw, 600px)',
          display: 'flex',
          flexDirection: 'column',

          '& .info-background': {
            height: 0,
            flexGrow: 1,

            '& .info-cover': {
              width: '100%',
              height: '100%',
              color: theme.palette.primary.contrastText,
              backgroundColor: fade(theme.palette.common.black, 0.5),
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              transition: '0.2s',

              '& .text': {
                fontSize: '2rem',
                position: 'relative',
                padding: '1em',
                borderWidth: 5,
                borderStyle: 'solid',
                borderColor: 'transparent',
                color: theme.palette.primary.contrastText,
                transition: '0.2s',
                cursor: 'pointer',

                '&:hover': {
                  borderColor: links[currentIndex].color,
                  color: links[currentIndex].color,
                },
              },

              '&:hover': {
                backgroundColor: fade(theme.palette.common.black, 0.7),
              },
            },
          },
        },
      },

      '& .dock-wrapper': {
        width: '100%',
        display: 'flex',

        '& .upholder': {
          height: 0,
          paddingBottom: `min(calc(120% / (${links.length - 1} * (1 + 0.1) + (0.1 + 0.1 + 1.2))), 120px)`,
        },

        '& .dock': {
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'flex-end',

          '& .link': {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            marginLeft: `min(calc(10% / ${links.length}), ${theme.spacing(1)}px)`,
            marginRight: `min(calc(10% / ${links.length}), ${theme.spacing(1)}px)`,
            borderRadius: '20%',
            transition: '0.2s',
            color: theme.palette.primary.contrastText,

            '& svg': {
              width: '60%',
              height: '60%',
              fontSize: 'inherit',
            },

            '& .upholder': {
              height: 0,
              paddingBottom: '100%',
            },

            '&:first-child': {
              marginLeft: 0,
            },

            '&:last-child': {
              marginRight: 0,
            },
          },

          '& .active': {
            width: 120,
            maxWidth: `calc(120% / (${links.length - 1} * (1 + 0.1) + (0.1 + 0.1 + 1.2)))`,
          },

          '& .inactive': {
            width: 100,
            maxWidth: `calc(100% / (${links.length - 1} * (1 + 0.1) + (0.1 + 0.1 + 1.2)))`,
          },
        },
      },
    },
  }));
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <div className="info-wrapper">
        <Card className="info">
          <CardHeader
            avatar={(
              <Avatar
                style={{
                  backgroundColor: links[currentIndex].color,
                }}
              >
                {links[currentIndex].icon}
              </Avatar>
            )}
            title={links[currentIndex].title}
            subheader={links[currentIndex].subtitle}
          />
          <CardMedia
            className="info-background"
            image={links[currentIndex].image}
          >
            <div className="info-cover">
              <Button
                className="text"
                onClick={() => {
                  navigateTo(links[currentIndex].path);
                }}
              >
                {`访问${links[currentIndex].title}`}
              </Button>
            </div>
          </CardMedia>
        </Card>
      </div>
      <div className="dock-wrapper">
        <div className="upholder" />
        <div className="dock">
          {links.map((link, index) => (
            <Paper
              key={link.title}
              className={`link ${currentIndex === index ? 'active' : 'inactive'}`}
              onMouseEnter={() => {
                setCurrentIndex(index);
              }}
              style={{
                fontSize: currentIndex === index ? 72 : 60,
                backgroundColor: link.color,
              }}
            >
              <div className="upholder" />
              {link.icon}
            </Paper>
          ))}
        </div>
      </div>
    </div>
  );
};

export default home;
