import React, { useEffect, useState } from 'react';
import {
  Paper,
  Card,
  CardHeader,
  Avatar,
  Button,
  CardActions,
  Divider,
  IconButton,
  Container,
  Theme,
  createStyles,
  makeStyles,
} from '@material-ui/core';
import { GetApp as DownloadIcon } from '@material-ui/icons';

import Loading from '@/ImperativeComponents/Loading';
import Message from '@/ImperativeComponents/Message';
import HeadBar from '@/Components/HeadBar';
import DropdownPanel from '@/Components/DropdownPanel';
import Empty from '@/Components/Empty';
import api from '@/Constants/api';
import request from '@/Utils/Request';

import Logo from '@/Assets/Images/Global/Logo.png';

interface FontStyle {
  id: number;
  name: string;
}
interface Font {
  id: number;
  fontFile: string;
  fontStyle: FontStyle;
  previewImage: string;
}

interface FontFamily {
  id: number;
  name: string;
  language: number;
  fonts: Font[];
}

const index: React.FC = () => {
  const [total, setTotal] = useState(0);
  const [fontFamilyList, setFontFamilyList] = useState<FontFamily[]>([]);
  const [searchKeyword, setSearchKeyword] = useState('');

  const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
      '& .header': {
        width: '100%',
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',

        '& .logo': {
          width: 200,

          [theme.breakpoints.up('md')]: {
            width: 300,
          },
        },

        '& .title': {
          fontSize: '2rem',
          margin: theme.spacing(4),

          [theme.breakpoints.up('md')]: {
            fontSize: '3rem',
          },
        },

        '& .intro': {
          fontSize: '1rem',

          [theme.breakpoints.up('md')]: {
            fontSize: '1.5rem',
          },
        },
      },

      '& .container': {
        display: 'flex',
        flexWrap: 'wrap',
        paddingTop: theme.spacing(1),
        paddingBottom: theme.spacing(1),

        [theme.breakpoints.only('xs')]: {
          width: 320,
        },

        '& .card-wrapper': {
          width: '100%',
          padding: theme.spacing(1),

          [theme.breakpoints.up('sm')]: {
            width: '50%',
          },

          [theme.breakpoints.up('md')]: {
            width: 'calc(100% / 3)',
          },

          [theme.breakpoints.up('lg')]: {
            width: '25%',
          },

          [theme.breakpoints.up('xl')]: {
            width: 'calc(100% / 6)',
          },

          '& .card-avatar': {
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.primary.contrastText,
          },

          '& .card-media': {
            width: '100%',
            padding: theme.spacing(1),
            filter: theme.palette.type === 'dark' ? 'invert(100%)' : undefined,
          },
        },
      },
    },

    cardActionPanel: {
      '& .font-image': {
        width: 240,
        margin: theme.spacing(1),
        marginBottom: 0,
        filter: theme.palette.type === 'dark' ? 'invert(100%)' : undefined,
      },

      '& .font-actions': {
        display: 'flex',
        justifyContent: 'space-between',

        '& .font-style': {
          marginLeft: theme.spacing(1),
        },
      },
    },
  }));
  const classes = useStyles();

  useEffect(() => {
    const loading = new Loading();
    request.get(api.fontLibrary.fontFamily.list, {
      params: searchKeyword ? {
        name: searchKeyword,
      } : undefined,
    }).then((res) => {
      loading.close();
      if (res.status === 200) {
        setFontFamilyList(res.data.list);
      } else {
        new Message({
          type: 'error',
          content: '获取字体失败',
        });
      }
    }).catch(() => {
      loading.close();
      new Message({
        type: 'error',
        content: '获取字体失败',
      });
    });
  }, [searchKeyword]);

  useEffect(() => {
    request.get(api.fontLibrary.fontFamily.list).then((res) => {
      if (res.status === 200) {
        setTotal(res.data.count);
      } else {
        new Message({
          type: 'error',
          content: '获取字体数量失败',
        });
      }
    }).catch(() => {
      new Message({
        type: 'error',
        content: '获取字体数量失败',
      });
    });
  }, []);

  return (
    <div className={classes.root}>
      <HeadBar
        position="fixed"
        searchText="搜索字体名称"
        onSearch={(keyword) => {
          setSearchKeyword(keyword);
        }}
        searchThrottle={500}
        changeTransparencyScrollHeight="100vh"
      />
      <Paper square className="header">
        <img className="logo" src={Logo} />
        <div className="title">洋芋田字体库</div>
        <div className="intro">
          {`现已收录 ${total} 种不同字体`}
        </div>
      </Paper>
      <Container fixed maxWidth="xl" className="container">
        {fontFamilyList.length ? fontFamilyList.map((fontFamily) => (
          <div key={fontFamily.id} className="card-wrapper">
            <Card>
              <CardHeader
                avatar={
                  <Avatar className="card-avatar">{fontFamily.language === 1 ? '中' : '英'}</Avatar>
                }
                title={fontFamily.name}
                titleTypographyProps={{ noWrap: true }}
                subheader={`共 ${fontFamily.fonts.length} 种样式可供下载`}
                action={(
                  <DropdownPanel dropdownElement={(
                    <IconButton>
                      <DownloadIcon />
                    </IconButton>
                  )}
                  >
                    {fontFamily.fonts.map((font, fontIndex) => (
                      <React.Fragment key={font.id}>
                        {fontIndex !== 0 && <Divider />}
                        <div className={classes.cardActionPanel}>
                          <img className="font-image" src={font.previewImage} />
                          <CardActions className="font-actions">
                            <div className="font-style">
                              字体样式：
                              {font.fontStyle.name}
                            </div>
                            <Button color="primary" href={font.fontFile} target="_blank">
                              下载
                            </Button>
                          </CardActions>
                        </div>
                      </React.Fragment>
                    ))}
                  </DropdownPanel>
                )}
              />
              {Boolean(fontFamily.fonts.length) && (
                <img className="card-media" src={fontFamily.fonts[0].previewImage} />
              )}
            </Card>
          </div>
        )) : (
          <Empty description="没有符合条件的字体" />
        )}
      </Container>
    </div>
  );
};

export default index;
