import React, { useEffect, useState } from 'react';
import {
  Avatar,
  Card,
  CardContent,
  CardHeader,
  Chip,
  Divider,
  Step,
  StepContent,
  StepLabel,
  Stepper,
  StepIconProps,
  Container,
  Typography,
  createStyles,
  makeStyles,
  Theme,
  CardActions,
  Button,
} from '@material-ui/core';
import { FiberNew as NewIcon } from '@material-ui/icons';

import Loading from '@/ImperativeComponents/Loading';
import Message from '@/ImperativeComponents/Message';
import HeadBar from '@/Components/HeadBar';
import request from '@/Utils/Request';
import api from '@/Constants/api';
import moment from 'moment';

import Logo from '@/Assets/Images/ImageToolkit/Logo.png';

interface Version {
  code: string;
  features: string;
  pubDate: Date;
  winPackage: string;
  macPackage: string;
}

const index: React.FC = () => {
  const [versionList, setVersionList] = useState<Version[]>([]);

  const useStyles = makeStyles((theme: Theme) => createStyles({
    root: {
      '& .stepper': {
        backgroundColor: 'unset',

        '& .step-icon-wrapper': {
          width: 24,
          height: 24,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',

          '& .active': {
            color: theme.palette.secondary.main,
          },

          '& .inactive': {
            width: 8,
            height: 8,
            borderRadius: 4,
            backgroundColor: theme.palette.text.secondary,
          },
        },
      },
    },
  }));
  const classes = useStyles();

  useEffect(() => {
    const loading = new Loading();
    request.get(api.imageToolkit.version.list).then((versionListRes) => {
      if (versionListRes.status === 200) {
        setVersionList(versionListRes.data.list);
        loading.close();
      } else {
        loading.close();
        new Message({
          type: 'error',
          content: '获取历史版本列表失败',
        });
      }
    }).catch(() => {
      loading.close();
      new Message({
        type: 'error',
        content: '获取历史版本列表失败',
      });
    });
  }, []);

  const renderStepIcon = (props: StepIconProps) => (
    <div className="step-icon-wrapper">
      {props.active ? <NewIcon className="active" /> : <div className="inactive" />}
    </div>
  );

  return (
    <div className={classes.root}>
      <HeadBar
        title="历史版本"
        changeTransparencyOnScroll={false}
      />
      <Container maxWidth="sm">
        <Stepper activeStep={0} orientation="vertical" className="stepper">
          {versionList.map((version) => (
            <Step expanded key={version.code}>
              <StepLabel StepIconComponent={renderStepIcon}>
                <Chip
                  label={moment(version.pubDate).format('YYYY 年 MM 月 DD 日')}
                  color="primary"
                  size="small"
                />
              </StepLabel>
              <StepContent>
                <Card>
                  <CardHeader
                    avatar={
                      <Avatar src={Logo} />
                    }
                    title={`版本号 ${version.code}`}
                    subheader={`发布于 ${moment(moment(new Date())).diff(version.pubDate, 'day')} 天前`}
                  />
                  <Divider />
                  <CardContent>
                    {version.features.split('\n').map((feature, featureIndex) => (
                      <Typography
                        key={feature}
                        align="justify"
                        variant="body2"
                        color="textSecondary"
                        paragraph={featureIndex < version.features.split('\n').length - 1}
                      >
                        {feature}
                      </Typography>
                    ))}
                  </CardContent>
                  <CardActions>
                    <Button
                      color="primary"
                      onClick={() => window.open(version.winPackage, '_blank')}
                    >
                      下载 Win 版
                    </Button>
                    {version.macPackage && (
                    <Button
                      color="primary"
                      onClick={() => window.open(version.macPackage, '_blank')}
                    >
                      下载 Mac 版
                    </Button>
                    )}
                  </CardActions>
                </Card>
              </StepContent>
            </Step>
          ))}
        </Stepper>
      </Container>
    </div>
  );
};

export default index;
