import React, { useState, useEffect, useContext } from 'react';
import {
  Theme,
  CssBaseline,
  ThemeProvider as MaterialThemeProvider,
  createMuiTheme,
} from '@material-ui/core';
import { blue, orange } from '@material-ui/core/colors';

import font from '@/Assets/Fonts/NotoSansSC-Thin.woff';

const NotoSansSC = {
  fontFamily: 'Noto Sans SC Thin',
  fontStyle: 'normal',
  fontWeight: 100,
  src: `url(${font}) format('woff')`,
};

interface ThemeContext {
  theme: Theme;
  darkMode: boolean | 'auto';
  setDarkMode: (option: boolean | 'auto') => void;
  getDarkMode: () => boolean;
}

const themeContext = React.createContext<ThemeContext>({
  theme: createMuiTheme(),
  darkMode: 'auto',
  setDarkMode: () => undefined,
  getDarkMode: () => true,
});

export const ThemeProvider: React.FC<{
  children:React.ReactNode,
}> = (props: { children:React.ReactNode }) => {
  const [theme, setTheme] = useState<Theme>(createMuiTheme());
  const [darkModeState, setDarkModeState] = useState<boolean | 'auto'>('auto');
  const { children } = props;

  const setDarkMode = (darkMode: boolean | 'auto') => {
    if (!darkMode) {
      window.localStorage.setItem('darkMode', '0');
    } else if (darkMode === true) {
      window.localStorage.setItem('darkMode', '1');
    } else {
      window.localStorage.removeItem('darkMode');
    }
    setDarkModeState(darkMode);
  };

  const getDarkMode = () => (
    localStorage.getItem('darkMode')
      ? Boolean(Number(localStorage.getItem('darkMode')))
      : matchMedia('(prefers-color-scheme: dark)').matches
  );

  useEffect(() => {
    const newTheme = createMuiTheme({
      palette: {
        primary: blue,
        secondary: orange,
        type: getDarkMode() ? 'dark' : 'light',
      },
      breakpoints: {
        values: {
          xs: 0,
          sm: 640,
          md: 960,
          lg: 1280,
          xl: 1920,
        },
      },
      typography: {
        fontFamily: 'Noto Sans SC Thin',
      },
      overrides: {
        MuiCssBaseline: {
          '@global': {
            '@font-face': [NotoSansSC],
          },
        },
      },
    });
    setTheme(newTheme);
  }, [darkModeState]);

  useEffect(() => {
    setDarkMode(Boolean(Number(localStorage.getItem('darkMode'))) || 'auto');
  }, []);

  return (
    <themeContext.Provider value={{
      theme,
      darkMode: darkModeState,
      setDarkMode,
      getDarkMode,
    }}
    >
      <MaterialThemeProvider theme={theme}>
        <CssBaseline />
        {children}
      </MaterialThemeProvider>
    </themeContext.Provider>
  );
};

const useTheme = (): ThemeContext => useContext(themeContext);

export default useTheme;
